.dropdown-items {
	position: absolute;
	background-color: #FFF;
	border: 1px solid var(--border-color);
	border-radius: 4px;
	margin-top: 0px;
	padding: 2px;
	-webkit-box-shadow: 0px 1px 3px rgba(0,0,0,.1);
	        box-shadow: 0px 1px 3px rgba(0,0,0,.1);
	z-index: -1;
	opacity: 0;
	left: 50%;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	    transform: translateX(-50%);

	-webkit-transition: margin-top .3s, opacity .3s, z-index .5s;

	-o-transition: margin-top .3s, opacity .3s, z-index .5s;

	transition: margin-top .3s, opacity .3s, z-index .5s;
}

.align-items-right .dropdown-items {
	left: unset;
	right: 0px;
	-webkit-transform: none;
	-ms-transform: none;
	    transform: none;
}

.align-items-left .dropdown-items {
	left: 0px;
	-webkit-transform: none;
	-ms-transform: none;
	    transform: none;
}

.dropdown-items.show {
	opacity: 1;
	z-index: 9996;
	margin-top: 15px;
}

.less-margin .dropdown-items.show {
	margin-top: 5px;
}

.dropdown-items::before {
    content: '';
    position: absolute;
    left: 50%;
    margin-left: -7px;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid #fff;
    top: -7px;
    z-index: 2;
}

.dropdown-items::after {
    content: '';
    position: absolute;
    left: 50%;
    margin-left: -9px;
    border-left: 9px solid transparent;
    border-right: 9px solid transparent;
    border-bottom: 9px solid var(--border-color);
    top: -9px;
}

.align-items-right .dropdown-items::after,
.align-items-right .dropdown-items::before,
.align-items-left .dropdown-items::after,
.align-items-left .dropdown-items::before {
	display: none;
}

